.head {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-direction: row;
  padding-inline: 20px;
}

select {
  width: max-content;
  height: 10;
}

.code-editor {
  padding: 20px;
  border-radius: 10px;
  background-color: rgba(0, 0, 0, 0.5); /* Transparent white background */
  backdrop-filter: blur(10px); /* Apply blur effect */
  resize: horizontal;
  overflow: auto;
}

.foot {
  display: flex;
  justify-content: end;
}

.foot button {
  margin: 10px;
  width: 100px;
  height: 30px;
  cursor: pointer;
}

.output {
  margin-block: 10px;
  border: 1px solid black;
  padding: 5px;
  border-radius: 12px;
}

.output h3 {
  display: flex;
  align-items: center;
}

.output img {
  cursor: pointer;
}

.rotate {
  transform: rotate(180deg);
}
